body {
  width: 100%;
  height: 100%;
  background-color: rgb(167, 161, 161);
}

.formulari {
  margin: 300px auto 0 650px;
  background-color: #fff;
  width: 500px;
  height: 500px;
  padding-top: 20px;
  padding-left: 30px;
}
.contingut {
  background-color: red;
  width: 440px;
  height: 450px;
}

.email {
  background-color: grey;
  border-radius: 5%;
}

.app {
  background-color: #fff;
  width: 330px;
  height: 570px;
  margin: 2em auto;
  border-radius: 5px;
  padding: 1em;
  position: relative;
  overflow: hidden;
  box-shadow: 0 6px 31px -2px rgba(0, 0, 0, 0.3);
}

a {
  text-decoration: none;
  color: #ffffff;
}

p {
  font-size: 13px;
  color: #333;
  line-height: 2;
}
.light {
  text-align: right;
  color: #fff;
}
.light a {
  color: #fff;
}

.bg {
  width: 400px;
  height: 550px;
  background: #257aa6;
  position: absolute;
  top: -5em;
  left: 0;
  right: 0;
  margin: auto;
  /* background-image: url("background.jpg"); */
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  clip-path: ellipse(69% 46% at 48% 46%);
}

form {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  text-align: center;
  padding: 2em;
}

header {
  width: 220px;
  height: 220px;
  margin: 1em auto;
}

form input {
  width: 100%;
  padding: 13px 15px;
  margin: 0.7em auto;
  border-radius: 100px;
  border: none;
  background: rgb(255, 255, 255, 0.3);
  font-family: "Poppins", sans-serif;
  outline: none;
  color: #fff;
}
input::placeholder {
  color: #fff;
  font-size: 13px;
}

.inputs {
  margin-top: -5em;
}

footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 2em;
  text-align: center;
}

button {
  width: 100%;
  padding: 13px 15px;
  border-radius: 100px;
  border: none;
  background: #257aa6;
  font-family: "Poppins", sans-serif;
  outline: none;
  color: #fff;
}
